@font-face {
  font-family: rajdhani-bold;
  src: url(./fonts/Rajdhani-Bold.ttf);
}

@font-face {
  font-family: rajdhani-medium;
  src: url(./fonts/Rajdhani-Medium.ttf);
}

@font-face {
  font-family: rajdhani-regular;
  src: url(./fonts/Rajdhani-Regular.ttf);
}

* {
  margin: 0;
  padding: 0;
  font-family: rajdhani-regular;
  letter-spacing: 0.03rem;
}

body {
  background-color: #dedede;
}

body.sticky {
  overflow: hidden;
}

.wrapper-black-bg {
  background: black;
  padding: 20px;
}

.wrapper-black-bg.carousel-wrapper {
  padding-bottom: 60px;
}

.wrapper-white-bg {
  /* background: #fff; */
  padding: 20px;
}

header.header-x {
  max-width: 977px;
  margin: 20px auto;
  display: flex;
  justify-content: space-between;
  background: #fff;
  border-radius: 5px;
  border: 1px solid;
  padding: 10px;
  align-items: center;
}

.main-wrapper-full {
  max-width: 997px;
  margin: auto;
}

.main-wrapper-full.about {
  max-width: 957px;
  background: #fff;
  border: 1px solid #f8aa57;
  border-radius: 5px;
  box-shadow: 1px 1px 8px #444;
  padding: 20px;
}

.header-x ul {
  display: flex;
  justify-content: space-evenly;
  height: 70px;
  align-items: center;
}

.header-x ul li {
  list-style: none;
  position: relative;
}

.header-x ul li ul {
  position: absolute;
  margin-left: 4px;
  display: none;
}

.header-x ul li:hover ul {
  display: flex;
}

.header-x ul li ul li a.btn-bg-black {
  margin: 0;
}

.btn-bg-black {
  border: 1px solid;
  margin: 10px 4px;
  padding: 12px 35px 15px 35px;
  background-color: #3e3d3d;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.6);
  color: #fff;
  font-size: 14px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  position: relative;
  transition: 0.5s all;
  text-decoration: none;
}

.btn-bg-black::after {
  position: absolute;
  content: "";
  height: 50%;
  width: 100%;
  top: 50%;
  left: 0;
  border-radius: 0 0 5px 5px;
  background: #00000040;
  pointer-events: none;
}

.btn-bg-black:hover,
.btn-bg-black.active {
  background-color: #ef7c02;
  color: #000;
  font-family: "rajdhani-medium";
}

.logo {
  display: flex;
  align-items: center;
}

.logo img {
  width: 130px;
}

.corousel img {
  height: 400px;
  width: 100%;
}

.corousel {
  margin: auto;
  max-width: 996px;
  position: relative;
  cursor: pointer;
}

.slick-dots {
  height: 33px;
  width: 100% !important;
}

.slick-dots li.slick-active button:before {
  opacity: 0.75 !important;
  color: #f67f00 !important;
}

.slick-dots li button:before {
  font-family: "slick";
  font-size: 6px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  content: "•";
  text-align: center;
  opacity: 0.25;
  color: #fff !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-dots li button {
  background: transparent !important;
}

.slick-arrow.slick-prev,
.slick-arrow.slick-next {
  display: none !important;
}

.side-header-nav {
  position: fixed;
  background: #000;
  opacity: 0.9;
  top: 0;
  width: 100vw;
  left: 0;
  display: flex;
  z-index: 999;
  height: 100vh;
  justify-content: center;
  overflow: auto;
  transition: all 0.5s;
}

.side-header-nav ul {
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.side-header-nav ul li {
  min-width: 180px;
  border-bottom: 2px solid #f67f00;
  padding: 10px;
  text-align: center;
}

.side-header-nav ul li ul {
  display: none;
  margin-top: 0;
}

.side-header-nav ul li:hover ul {
  display: block;
  margin-top: 0;
}

.side-header-nav ul li ul li {
  padding: 10px 0;
  border: none;
}

.side-header-nav ul li a {
  text-decoration: none;
  color: #fff;
}

.header-x ul.hamburger-link {
  display: none;
}

.dropdown {
  position: relative;
}

.dropdown::before {
  content: "";
  position: absolute;
  background-image: url(../src/images/icons/arrow-down.svg);
  height: 15px;
  width: 20px;
  right: -20px;
  top: 0;
  display: block;
}

.btn-bg-black.dropdown::before {
  right: 10px;
  top: 7px;
}

li:hover .dropdown::before {
  background-image: url(../src/images/icons/arrow-up.svg);
}

.card-holder {
  display: flex;
  max-width: 997px;
  margin: auto;
  flex-wrap: wrap;
  justify-content: space-between;
  transform: translateY(-27px);
}

.footer {
  max-width: 996px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-icon ul {
  display: flex;
}

.footer-icon ul li {
  list-style: none;
}

.footer-icon img {
  width: 35px;
}

.service-image img {
  width: 100%;
  max-height: 200px;
}

.category-heading {
  margin-top: 10px;
}

.service-header h1,
h1.blog-title {
  font-size: 30px;
}

.service-header {
  margin-bottom: 16px;
}

.service-card {
  width: 44%;
  padding: 20px;
  background: #fff;
  border: 1px solid #f8aa57;
  border-radius: 5px;
  box-shadow: 1px 1px 8px #444;
  margin: 10px 0px 83px 1px;
}

.about-content {
  margin-bottom: 20px;
}

.service-content button {
  margin-bottom: 10px;
  margin-top: 10px;
  width: 149px;
  /* height: 36px; */
}

.service-content span {
  font-weight: bold;
}

.whole_loop {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.card {
  max-width: 300px;
  padding: 15px 10px;
  border: 1px solid #888;
  border-radius: 5px;
  box-shadow: 1px 1px 8px #444;
  background-color: #fff;
}

.card-header {
  font-family: "rajdhani-medium";
  font-size: 30px;
  margin-bottom: 15px;
}

.card-img {
  max-width: 100%;
  border: 2px solid #fff;
  transform: translate(-2px, -2px);
  box-shadow: 1px 1px 5px #333;
  border-radius: 5px;
}

.card-content {
  font-size: 14px;
  padding: 15px 0;
}

.contact_wrapper {
  display: flex;
  justify-content: space-between;
}

.corporate-address ul {
  display: flex;
  justify-content: space-evenly;
}

.corporate-address ul li {
  list-style: none;
}

.corporate-address ul li img {
  width: 24px;
  float: left;
}

.conatctus-page {
  width: 61%;
  height: 40%;
  top: 79px;
  left: 20%;
  position: fixed;
  background: #fff;
  padding: 60px 20px 20px;
  border-radius: 5px;
  box-shadow: 2px 2px 6px #000;
}

.contact-form {
  width: 52%;
}

.close-btn img {
  width: 30px;
}

.close-btn {
  height: 35px;
  background: transparent;
  border: 0;
}

.contact-heading {
  position: absolute;
  right: 15px;
  top: 15px;
}

.card-holder .card-header {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.card-holder .card-img {
  width: 100%;
  height: 150px;
}

.submit-btn {
  width: 111px;
  height: 31px;
}
.submit-btn:hover {
  background-color: #f67f00;
}

form input {
  margin-bottom: 10px;
  width: 99%;
  height: 35px;
  background-color: black;
  cursor: pointer;
  padding: 3px 3px 3px 9px;
}

form input:hover {
  background-color: rgb(248, 159, 6);
}

form textarea {
  width: 99%;
  height: 70px;
  background-color: black;
  cursor: pointer;
  padding: 3px 3px 3px 9px;
}

button {
  cursor: pointer;
}

.overley {
  position: fixed;
  top: 0;
  left: 0;
  background: #000000cf;
  width: 100%;
  height: 100%;
  filter: blur(0.002px);
}

form textarea:hover {
  background-color: rgb(248, 159, 6);
}

/* iphone 12 pro */
@media screen and (max-width: 844px) {
  .header-x ul.upper-nav-links {
    display: none;
  }

  .header-x ul.hamburger-link {
    z-index: 1000;
    display: flex;
  }

  .corporate-address {
    display: none;
  }

  .contact-form {
    width: 211px;
    margin: auto;
  }

  .main-wrapper-full {
    width: 322px !important;
    margin: auto;
  }

  .service-card {
    width: 280px !important;
  }
}
